import React from "react";
import './aboutMe.css';

const AboutMe = () => {
    return (
        <div className='aboutMeContent'>

            <div>
                <h2>
                    Über mich
                </h2>
                <p>
                    Ich komme aus einem kleinen Dorf namens Alladorf. Dort lebe ich mit meiner Mutter, unseren beiden
                    Hunden und meinen beiden Katzen. In meiner Freizeit spiele ich gerne Computerspiele, alleine mit
                    Musik oder zusammen mit Freunden. Gerne geniese ich auch die Natur bei einem Spaziergang, natürlich
                    mit den Hunden. Sehr gerne Koche ich auch, am besten frisch und sleber gemacht. Zu meinen
                    speziallitäten gehören Burger mit selbst gebackenen Buns und Salat und Gurken aus dem eigenen
                    Garten.
                </p>
            </div>

            <div>
                <h2>
                    Projekte
                </h2>
                <h3>Azul</h3>
                <p>
                    Dieses Projekt war Teil einer Gruppenstudienarbeit, bestehent aus einem Team von vier Leuten, im
                    Fach Software Engineering II. Die Aufgabe war es, dass Brettspiel Azul, gemäß der offizielen Regeln
                    zu
                    Implementieren. Zudem sollte es auch noch die Möglichkeit geben Spiele zu speichern und laden.
                    <br/>
                    Das Projekt wurde mit dem Vorgehensmodell von Scrum durchgeführt. Die Software wurde mit Java
                    Implementiert.
                </p>
                <h3>Partyplaner</h3>
                <p>
                    Ziel des Projektes war, eine Software zu entwickeln, die unterstützung bietet, bei der Planung von
                    Parties. Die Software bot Funktionen für, anlegen einer Party, Aufgabenverwaltung, Helferverwaltung,
                    Schichtplanung, Budgetplanung, Archiviren von Parties mit Bildern.
                    <br/>
                    Das Projekt wurde agile mit Scrum entwickelt und umgesetzt wurde es mit Java Spring.
                </p>
                <h3>Stundenplanverwaltung</h3>
                <p>
                    Ziel des Projektes war, eine Webanwendung zu entwickeln, die die Möglichkeit bietet, den Stundenplan
                    anzeigen zu lassen zu Ausgewählten Studienfach und Semester.
                    <br/>
                    Das Projekt wurde mit Laravel umgesetzt.
                </p>
                <h3>Persönliche Website</h3>
                <p>

                </p>
            </div>


            <div>
                <h2>
                    Schulische Laufbahn
                </h2>
                <p>
                    Nach der Grundschule habe ich auf die Gesamtschule Hollfeld. Mathe war immer eins meiner
                    Lieblingsfächer, was auch dazu beigetragen hat, dass ich vorerst auf Gymnasial Niveau eingestuft
                    wurde, in Deutsch und Englisch jedoch Hauptschul Niveau.
                </p>
            </div>

            <div>
                <h2>
                    Studium
                </h2>
            </div>
        </div>

    );
}

export default AboutMe;
